.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(10, 10, 10, 0.8);
  
  /* allow vertical scroll if needed */
  overflow-y: auto;

  display: flex;
  justify-content: center;
  align-items: flex-start;  /* Changed from center */

  padding-top: 60px;  /* adjust this to match your navbar height */
  padding-bottom: 60px; /* so modal doesn’t overlap footer on tall screens */

  z-index: 999;
}

/* Modal content */
.modal-content-income,
.modal-content-savings {
  background-color: #000;
  color: white;
  padding: 1rem 1.2rem;
  border-radius: 12px;
  border: 1px solid rgb(142, 102, 252);
  box-shadow: 0 0 20px rgba(225, 223, 229, 0.5);

  width: 85%;
  max-width: 450px;
  
  /* Limit height so modal doesn’t overflow viewport */
  max-height: calc(100vh - 120px); /* viewport height minus top+bottom padding */
  
  /* enable internal scrolling if content overflows */
  overflow-y: auto;

  font-family: 'Russo One', sans-serif;
  position: relative;
  transition: all 0.3s ease;

  /* remove negative margin-top */
  margin-top: 0;
}

  
  .modal-content-income.tall,
  .modal-content-income.expanded,
  .modal-content-savings.tall,
  .modal-content-savings.modal-content-income.expanded {
    min-height: 160px; 
  }
  
  .modal-content-income h2,
  .modal-content-savings h2 {
    margin-bottom: 0.8rem;
    font-size: 1.2rem;
    color: #8a2be2;
    text-align: center;
  }
  
  label {
    display: block;
    margin-top: 0.6rem;
    font-size: 0.85rem;
    color: #d6d6f5;
  }
  
  
  .modal-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    gap: 0.6rem;
  }

  .modal-buttons {
    display: flex;
    justify-content: center;  
    gap: 0.6rem;             
  }
  
  .modal-buttons button {
    padding: 0.5rem 0.8rem;
    font-size: 0.8rem;
    font-weight: bold;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s;
    width: auto;
    min-width: 80px;
  }
  
  .modal-buttons button[type="submit"] {
    background-color: #8a2be2;
    color: #fff;
  }
  
  .modal-buttons button[type="submit"]:hover {
    background-color: #a64cf5;
  }
  
  .modal-buttons button[type="button"] {
    background-color: #444;
    color: #fff;
  }
  
  .modal-buttons button[type="button"]:hover {
    background-color: #666;
  }

  .success-message {
    color: #4caf50; /* Green text for success */
    border-radius: 8px;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 1rem;
    margin-bottom: 0;
  }

input[type="text"],
input[type="number"] {
  width: 80%;
  padding: 0.45rem;
  margin-top: 0.4rem;
  border: 1px solid #555;
  border-radius: 6px;
  background-color: #2b2b3d;
  color: #fff;
  font-size: 0.85rem;
  padding-left: 0.7rem; 
}


input:focus,
select:focus {
  border-color: #8a2be2;
  outline: none;
}


.form-group-income,
.form-group-category select,
.form-group-monthly select {
  width: 80%;
  padding: 0.45rem;
  margin-top: 0.4rem;
  border: 1px solid #555;
  border-radius: 6px;
  background-color: #2b2b3d;
  color: #fff;
  font-size: 0.85rem;
  padding-left: 0.7rem; 
  margin-bottom: 1rem;
}

.form-group-custom {
    width: 93%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -1rem;
    margin-bottom: 1rem;
  }

  .form-group-add {
    width: 50%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.5rem;
  }


  input[type="month"] {
  width: 80%;
  padding: 0.45rem;
  margin-top: 0.4rem;
  border: 1px solid #555;
  border-radius: 6px;
  background-color: #2b2b3d;
  color: #fff;
  font-size: 0.85rem;
  padding-left: 0.7rem; 
  cursor: pointer;
}

.form-group-assign{
    width: 93%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -1rem;
    margin-bottom: 2rem;
  }
  
.modal-content-income .success-message,
.modal-content-savings .success-message {
    color: #4caf50; 
    border-radius: 8px;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 1rem;
    margin-bottom: 0;
  }

select {
  /* Remove default appearance on most browsers */
  -webkit-appearance: none;  /* iOS Safari */
  -moz-appearance: none;     /* Firefox */
  appearance: none;
}

  @media (min-width: 250px) and (max-width: 700px){



  .modal-content-income h2,
  .modal-content-savings h2 {
    font-size: 1.8rem;
    padding-bottom: 0.5rem;
  }

    .modal-content-income,
  .modal-content-savings {
    width: 85%;            /* Slightly wider on mobile */
    max-width: none;
    padding: 1rem 1.2rem 3rem 1.2rem; /* Add extra bottom padding for scroll */
    
    /* Reduce max-height so modal fits inside viewport minus padding */
    max-height: calc(100vh - 190px);

    overflow-y: auto;      /* Enable vertical scroll if needed */
  }
    
  }

  @media (min-width: 1801px){

    .modal-content-income {
        margin-top: -15rem;
      }

  }

  
  